import React from "react"
import { Helmet } from "react-helmet"

import Layout from "../components/layout"
import SEO from "../components/seo"
import IndexSearch from "../components/index-search"

import Container from "react-bootstrap/Container"
import { AdPlaceholder } from "../components/ad"

const IndexPage = () => {
  return(
    <Layout activeIndex='home'>
      <Helmet>
        <script async src="//pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
        <script>{`(adsbygoogle = window.adsbygoogle || []).push({google_ad_client: 'pub-5810343997734616', enable_page_level_ads: true });`}</script>
      </Helmet>
      <SEO title="Home" />
      <Container className="my-2">
        <AdPlaceholder />
      </Container>
      <Container className="my-5">
        <IndexSearch />
      </Container>
      <Container className="my-2">
        <AdPlaceholder />
      </Container>
    </Layout>
  )
}

export default IndexPage
