import React from 'react'

import { useStaticQuery, navigate, graphql } from 'gatsby'

import { Jumbotron, Form } from 'react-bootstrap'
import { Typeahead } from 'react-bootstrap-typeahead'
import "react-bootstrap-typeahead/css/Typeahead.css"

const IndexSearch = () => {

  const data = useStaticQuery(graphql`
    {
      allChampionsJson(sort: {fields: name}) {
        edges {
          node {
            name
            fields {
              pathName
            }
            thumbnailImg {
              childImageSharp {
                fixed(width: 120, quality: 100) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  `);

  const filterByNameQuery = (option, {text, options}) => {
    return options.filter(option => option.node.name.toLowerCase().indexOf(option.toLowerCase()) !== -1);
  }

  const origData = data.allChampionsJson.edges;

  const goTo = (obj) => {
    if (obj && obj[0]){
      console.log(obj[0])
      navigate(obj[0].node.fields.pathName);
    }
  }

  const headStyle = {
    textAlign: `center`,
    marginBottom: `1em`
  }

  return(
    <Jumbotron >
      <h1 style={headStyle}>Champion Search</h1>
      <Form>
        <Form.Group>
          <Typeahead
            labelKey={option => option.node.name}
            placeholder="Champion Name"
            options={origData}
            clearButton
            bsSize="large"
            highlightOnlyResult
            selectHintOnEnter
            onChange={goTo}
            className="add-shadow-orange"
          />
        </Form.Group>
      </Form>
    </Jumbotron>
  );
  
}

export default IndexSearch;